import React from "react";
import Post from "./Post";

const PostList = ({ recipes }) => {
	const postArray = recipes.map((singlePost, i) => {
		return (
			<Post
				key={i}
				name={recipes[i].name}
				source={recipes[i].source}
				ingredients={recipes[i].ingredients}
				recipe={recipes[i].recipe}
			/>
		);
	});

	return <div>{postArray}</div>;
};

export default PostList;
